import React, { useState, useEffect } from 'react'
import { useHistory, useParams, Link } from 'react-router-dom'
import axios from 'axios'
import BarChart from '../../components/BarChart/BarChart'
import AreaChart from '../../components/AreaChart/AreaChart'
import PieChart from '../../components/PieChart/PieChart'
import './index.less'
import './detail.less'
import Table from '../../components/Table/Table'
import { formatAddress, toPlainString, isV3 } from '../../utils/utils'
import DeriNumberFormat from '../../utils/DeriNumberFormat'
import moment from 'moment'
import Filter from './Filter';
import symbolConfig from '../../symbol.json'

const GET_LIQUIDITY_URL = `${process.env.REACT_APP_INFO_HTTP_URL}`

const tradeColums = [
  {
    title: 'DIRECTION',
    dataIndex: 'direction',
    key: 'direction',
    width: '14%',
    render: value => <span className={`direction ${value}`}>{value.toUpperCase()}</span>,
  },
  {
    title: 'ACCOUNT',
    dataIndex: 'account',
    key: 'account',
    width: '14%',
    render: value => value ? formatAddress(value) : '',
  },
  {
    title: 'SYMBOL',
    dataIndex: 'symbol',
    key: 'symbol',
    width: '14%',
    render: value => symbolConfig[value] && symbolConfig[value].displayName || value
  },
  {
    title: 'PRICE',
    dataIndex: 'price',
    key: 'price',
    width: '14%',
    render: value => <DeriNumberFormat value={value} decimalScale={5} />
  },
  {
    title: 'VOLUME',
    dataIndex: 'volume',
    key: 'volume',
    width: '14%',
    render: value => toPlainString(value)
  },
  {
    title: 'NOTIONAL',
    dataIndex: 'notional',
    key: 'notional',
    width: '14%',
    render: (value, row) => <DeriNumberFormat value={value} prefix='$' decimalScale={5} />
  },
  {
    title: 'TIMESTAMP (UTC)',
    dataIndex: 'timestamp',
    key: 'timestamp',
    width: '14%',
    render: value => moment(new Date(value * 1000)).utc().format('YYYY-MM-DD HH:mm:ss')
  },
]
const liqColums = [
  {
    title: 'ACTION',
    dataIndex: 'action',
    key: 'action',
    width: '20%',
    render: value => <span className={`action ${value}`}>{value.toUpperCase()}</span>,
  },
  {
    title: 'ACCOUNT',
    dataIndex: 'account',
    key: 'account',
    width: '20%',
    render: value => value ? formatAddress(value) : '',
  },
  {
    title: 'LIQUIDITY',
    dataIndex: 'notional',
    key: 'notional',
    width: '20%',
    render: (value, row) => row.amount === 'MAX_VALUE' ? row.amount : <DeriNumberFormat value={value} prefix='$' decimalScale={5} />
  },
  {
    title: 'AMOUNT',
    dataIndex: 'amount',
    key: 'amount',
    width: '20%',
    render: (value, row) => row.amount === 'MAX_VALUE' ? row.amount : <DeriNumberFormat value={value} suffix={` ${row.bToken}`} decimalScale={5} />
  },
  {
    title: 'TIMESTAMP (UTC)',
    dataIndex: 'timestamp',
    key: 'timestamp',
    width: '20%',
    render: value => moment(new Date(value * 1000)).utc().format('YYYY-MM-DD HH:mm:ss'),
  }
]

export default function Detail() {
  const { network, add, catalog, bToken } = useParams();
  const baseToken = /(\w+\|)+/.test(bToken) ? 'MULTIPLE TOKEN' : bToken
  const [getTradeHistoryUrl, setGetTradeHistoryUrl] = useState(`${process.env.REACT_APP_INFO_HTTP_URL}/get_trade_history?pool=${add}`);
  const [getLpNetValue, setGetLpNetValue] = useState(`${process.env.REACT_APP_INFO_HTTP_URL}/get_liquidity_netvalue?pool=${add}`)
  const [getTokens, setGetTokens] = useState(`${process.env.REACT_APP_INFO_HTTP_URL}/get_tokens?pool=${add}`)
  const getLiquidityDataUrl = `${GET_LIQUIDITY_URL}/get_liquidity?pool=${add}`
  const getTradeDataUrl = `${GET_LIQUIDITY_URL}/get_trade?pool=${add}`

  return (
    <div className='info-main info-detail'>
      <div className='title'><Link to='/info'>Deri Overview</Link> &gt; {`${network} - ${catalog.toUpperCase()} - ${formatAddress(add)} (${baseToken})`}</div>
      <div className='charts'>
        <div className='graph tvl'>
          <AreaChart title='TVL' url={`${process.env.REACT_APP_INFO_HTTP_URL}/get_liquidity_history?pool=${add}`} />
        </div>
        <div className='graph volume24'>
          <Filter url={getTradeHistoryUrl} onChange={url => setGetTradeHistoryUrl(url)} />
          <BarChart title='Volume 24H' url={getTradeHistoryUrl} cycle={['WEEKLY', 'MONTHLY']} defaultCycle='MONTHLY' />
        </div>
      </div>
      {isV3(add) && <div className='charts'>
        <div className='graph netValue'>
          <Filter url={getLpNetValue} onChange={url => setGetLpNetValue(url)} cycleArr={['7D', '15D', "ALL"]} />
          <AreaChart title='LP Net Value' height={350} url={getLpNetValue} domain={true} yMin={0.0001} yMax={0.0001} decimalScale={4} tip={network === "BSC" ? "This is the BUSD value you would have as of today if you had provided 1BUSD as liquidity at the pool launch. It contains the trading PnL from Deri Protocol and the interest yield from Venus. Please note that your mining harvests of DERI and XVS are NOT included in this Net Value. If you provided tokens other than BUSD (i.e. BNB), the net value might be slightly different as the interest rate depends on tokens." : "This is the USDC value you would have as of today if you had provided 1USDC as liquidity at the pool launch. It contains the trading PnL from Deri Protocol and the interest yield from AAVE. Please note that your mining harvests of DERI are NOT included in this Net Value. If you provided tokens other than USDC (i.e. BTC), the net value might be slightly different as the interest rate depends on tokens."} />
        </div>
        <div className='graph tokens'>
          <Filter className="tokens-filter" url={getTokens} onChange={url => setGetTokens(url)} cycleArr={['TRADER', 'LP']} />
          <PieChart title='Top Margin/Liquidity' url={getTokens} isInfo={true} />
        </div>
      </div>}

      <div className='list-by-chain'>
        <Table title='LIQUIDITY' columns={liqColums} url={getLiquidityDataUrl} pagination />
        <Table title='TRADE' columns={tradeColums} url={getTradeDataUrl} pagination />
      </div>
    </div>
  )
}